.DatePickerPage{min-height:800px;position:absolute;padding-top:70px;width:100%;background:-webkit-gradient(linear, right top, left top, from(#c5d5cb), to(#e3e0cf));background:linear-gradient(to left, #c5d5cb, #e3e0cf)}.DatePickerPage .datePickerPage .home .dp-logo{height:80px}.DatePickerPage .datePickerPage h1{color:#213e49;font-family:'Lato';font-weight:bold;padding-bottom:100px}.DatePickerPage .datePickerPage .calendar{max-width:100%;padding-top:50px;padding-bottom:50px;display:block;height:auto;margin:0 auto;box-shadow:4px 4px 8px 0px rgba(0,0,0,0.507)}.DatePickerPage .datePickerPage p{color:#213e49;font-family:'Lato';font-size:20px;margin-bottom:0px}.DatePickerPage .datePickerPage .btn-val{border:0px;height:40px;width:100px;font-family:'Lato';background-color:#213e49;color:#f1f1f3;border:none;margin-top:35px}.DatePickerPage .datePickerPage .btn-val:hover{background-color:#4297cf;-webkit-transition:.1s ease-out;transition:.1s ease-out;outline:none}.DatePickerPage .datePickerPage .btn-val:after{outline:none;border:none}

